<template>
    <v-app>
        <v-main v-if="status==='auth'">
            <MainView/>
        </v-main>
        <v-main v-else-if="status==='unauth'">
            <LogIn
            @register="register"
            />
        </v-main>
        <v-main v-else>
            <RegisterUser
            @registerSuccess="registerSuccess"
            />
        </v-main>
    </v-app>
</template>

<script>
import MainView from '@/views/Main.vue'
import LogIn from '@/views/Auth/LogIn.vue'
import RegisterUser from '@/views/Auth/RegisterUser.vue'

// import axios from "axios";

export default {
  name: 'App',

    data: () => ({
        status : "unauth"
    }),
    created : function(){
        //  jwt      
        const token = localStorage.getItem('access_token')
        
        //   . 
        if (localStorage.getItem("access_token")){
            const expired = (JSON.parse(atob(localStorage.getItem("access_token").split('.')[1]))).exp
            //    
            if (expired >= Date.now()/1000){
                this.status = "auth"
                this.$router.push({"name" : "projects"})
            } else {
                this.status = "unauth"
            }
        //  . token 
        }
        token
        

    },


    components: {
        MainView,
        LogIn,
        RegisterUser
    },

    methods : {
        register : function(status){
            this.status = status
        },
        registerSuccess : function(status){
            this.status = status
        }
    }


}
</script>
